.icon {
  width: 30px;
  height: 30px;
  padding: 5px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  @include hover(transform, scale($transform-scale));

  &--cart {
    @include on-tablet {
      display: none;
    }
  }

  &--menu {
    display: none;
    background-image: url(../images/icons/icon-menu.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @include hover(
      background-image,
      url(../images/icons/icon-burger-menu-hover.svg)
    );

    @include on-tablet {
      display: block;
    }
  }

  &--close {
    width: 12px;
    height: 12px;

    @include hover(transform, rotate(30deg));
  }

  &--tw {
    background-image: url(../images/icons/social/icon-tw.svg);
  }

  &--fb {
    background-image: url(../images/icons/social/icon-fb.svg);
  }

  &--ig {
    background-image: url(../images/icons/social/icon-ig.png);
  }
}
